'use client';

import { useState, useEffect } from 'react';
import { useRouter, useParams } from 'next/navigation';
import { ChevronRight, Plus, Trash2, GripVertical } from 'lucide-react';

interface Question {
  id: string;
  type: 'text' | 'textarea' | 'radio' | 'checkbox' | 'dropdown';
  label: string;
  required: boolean;
  options?: string[];
}

interface FormData {
  id: string;
  title: string;
  description: string;
  settings: {
    currentlyReceiving: boolean;
    canBeEdited: boolean;
    fillOnce: boolean;
  };
  openDate: string;
  closeDate: string;
  questions: Question[];
  creator: string;
  created: string;
  updated: string;
  responseCount: number;
  status: 'Open' | 'Closed';
}

export default function CreateEditFormPage() {
  const router = useRouter();
  const params = useParams();
  const isEdit = !!params?.id;

  const [formData, setFormData] = useState<FormData>({
    id: '',
    title: 'Formulir Baru',
    description: '',
    settings: {
      currentlyReceiving: true,
      canBeEdited: false,
      fillOnce: true
    },
    openDate: '',
    closeDate: '',
    questions: [],
    creator: 'Iqbal',
    created: '',
    updated: '',
    responseCount: 0,
    status: 'Open'
  });

  // Load form data if editing
  useEffect(() => {
    if (isEdit) {
      const savedForms = localStorage.getItem('forms');
      if (savedForms) {
        const forms = JSON.parse(savedForms);
        const form = forms.find((f: FormData) => f.id === params.id);
        if (form) {
          setFormData(form);
        }
      }
    }
  }, [isEdit, params?.id]);

  const handleInputChange = (field: string, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleSettingChange = (setting: keyof FormData['settings']) => {
    setFormData(prev => ({
      ...prev,
      settings: {
        ...prev.settings,
        [setting]: !prev.settings[setting]
      }
    }));
  };

  const addQuestion = () => {
    const newQuestion: Question = {
      id: Date.now().toString(),
      type: 'text',
      label: '',
      required: false
    };
    setFormData(prev => ({
      ...prev,
      questions: [...prev.questions, newQuestion]
    }));
  };

  const updateQuestion = (questionId: string, field: string, value: any) => {
    setFormData(prev => ({
      ...prev,
      questions: prev.questions.map(q =>
        q.id === questionId ? { ...q, [field]: value } : q
      )
    }));
  };

  const removeQuestion = (questionId: string) => {
    setFormData(prev => ({
      ...prev,
      questions: prev.questions.filter(q => q.id !== questionId)
    }));
  };

  const addOption = (questionId: string) => {
    setFormData(prev => ({
      ...prev,
      questions: prev.questions.map(q =>
        q.id === questionId
          ? { ...q, options: [...(q.options || []), ''] }
          : q
      )
    }));
  };

  const updateOption = (questionId: string, optionIndex: number, value: string) => {
    setFormData(prev => ({
      ...prev,
      questions: prev.questions.map(q =>
        q.id === questionId
          ? {
              ...q,
              options: q.options?.map((opt, idx) =>
                idx === optionIndex ? value : opt
              )
            }
          : q
      )
    }));
  };

  const removeOption = (questionId: string, optionIndex: number) => {
    setFormData(prev => ({
      ...prev,
      questions: prev.questions.map(q =>
        q.id === questionId
          ? {
              ...q,
              options: q.options?.filter((_, idx) => idx !== optionIndex)
            }
          : q
      )
    }));
  };

  const handleSubmit = () => {
    if (!formData.title.trim()) {
      alert('Title harus diisi!');
      return;
    }

    const now = new Date().toLocaleDateString('id-ID', {
      day: 'numeric',
      month: 'short',
      year: 'numeric'
    });

    const savedForms = localStorage.getItem('forms');
    const forms = savedForms ? JSON.parse(savedForms) : [];

    if (isEdit) {
      // Update existing form
      const updatedForms = forms.map((f: FormData) =>
        f.id === formData.id
          ? { ...formData, updated: now }
          : f
      );
      localStorage.setItem('forms', JSON.stringify(updatedForms));
    } else {
      // Create new form
      const newForm = {
        ...formData,
        id: Date.now().toString(),
        created: now,
        updated: now,
        link: `F3fOkK`
      };
      forms.push(newForm);
      localStorage.setItem('forms', JSON.stringify(forms));
    }

    alert('Formulir berhasil disimpan!');
    router.push('/admin/form');
  };

  const questionTypeOptions = [
    { value: 'text', label: 'TEXT' },
    { value: 'textarea', label: 'TEXTAREA' },
    { value: 'radio', label: 'RADIO' },
    { value: 'checkbox', label: 'CHECKBOX' },
    { value: 'dropdown', label: 'DROPDOWN' }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Breadcrumb */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span onClick={() => router.push('/')} className="hover:text-gray-900 cursor-pointer">Home</span>
            <ChevronRight className="w-4 h-4" />
            <span onClick={() => router.push('/admin')} className="hover:text-gray-900 cursor-pointer">Admin</span>
            <ChevronRight className="w-4 h-4" />
            <span onClick={() => router.push('/admin/form')} className="hover:text-gray-900 cursor-pointer">Form</span>
            <ChevronRight className="w-4 h-4" />
            <span className="text-gray-900 font-medium">{isEdit ? formData.id : 'New'}</span>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-6 py-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-8">Edit Formulir</h1>

        <div className="bg-white rounded-lg shadow-sm p-8 mb-6">
          {/* Title */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Title<span className="text-red-500">*</span>
            </label>
            <input
              type="text"
              value={formData.title}
              onChange={(e) => handleInputChange('title', e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
              placeholder="Form Feedback acara"
            />
          </div>

          {/* Description */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Description
            </label>
            <textarea
              value={formData.description}
              onChange={(e) => handleInputChange('description', e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none resize-none"
              rows={3}
              placeholder="Mohon untuk mengisi agar acara berikutnya lancar"
            />
          </div>

          {/* Settings */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-3">
              Pengaturan
            </label>
            <div className="space-y-3">
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="checkbox"
                  checked={formData.settings.currentlyReceiving}
                  onChange={() => handleSettingChange('currentlyReceiving')}
                  className="w-4 h-4 text-red-500 border-gray-300 rounded focus:ring-red-500"
                />
                <span className="text-sm text-gray-700">Formulir sedang menerima jawaban</span>
              </label>
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="checkbox"
                  checked={formData.settings.canBeEdited}
                  onChange={() => handleSettingChange('canBeEdited')}
                  className="w-4 h-4 text-red-500 border-gray-300 rounded focus:ring-red-500"
                />
                <span className="text-sm text-gray-700">Jawaban dapat dirubah</span>
              </label>
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="checkbox"
                  checked={formData.settings.fillOnce}
                  onChange={() => handleSettingChange('fillOnce')}
                  className="w-4 h-4 text-red-500 border-gray-300 rounded focus:ring-red-500"
                />
                <span className="text-sm text-gray-700">Hanya dapat mengisi sekali</span>
              </label>
            </div>
          </div>

          {/* Open Date */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Dibuka pada
            </label>
            <input
              type="datetime-local"
              value={formData.openDate}
              onChange={(e) => handleInputChange('openDate', e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
            />
          </div>

          {/* Close Date */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Ditutup pada
            </label>
            <input
              type="datetime-local"
              value={formData.closeDate}
              onChange={(e) => handleInputChange('closeDate', e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
            />
          </div>

          {/* Save Button */}
          <button
            onClick={handleSubmit}
            className="w-full bg-red-500 hover:bg-red-600 text-white font-medium py-3 rounded-lg transition-colors"
          >
            Simpan
          </button>

          {/* Stats */}
          <div className="mt-6 flex items-center gap-4 text-sm">
            <span className="text-gray-600">
              Total jawaban: <span className="font-semibold">{formData.responseCount}</span>
            </span>
            <button className="px-4 py-2 bg-gray-200 hover:bg-gray-300 text-gray-700 rounded-lg transition-colors">
              Hapus semua jawaban {formData.responseCount}
            </button>
          </div>
        </div>

        {/* Questions Section */}
        <div className="bg-white rounded-lg shadow-sm p-8">
          <h2 className="text-xl font-bold text-gray-900 mb-6">Pertanyaan</h2>

          {formData.questions.length === 0 ? (
            <p className="text-gray-500 text-center py-8">
              Belum ada pertanyaan. Klik tombol di bawah untuk menambahkan pertanyaan.
            </p>
          ) : (
            <div className="space-y-6 mb-6">
              {formData.questions.map((question, index) => (
                <div
                  key={question.id}
                  className="border border-gray-200 rounded-lg p-6 hover:border-gray-300 transition-colors"
                >
                  <div className="flex items-start gap-3 mb-4">
                    <GripVertical className="w-5 h-5 text-gray-400 mt-2 cursor-move" />
                    <div className="flex-1">
                      <div className="flex items-center gap-2 mb-2">
                        <span className="text-sm font-semibold text-gray-700">No. {index + 1}</span>
                      </div>
                      <div className="mb-4">
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Jenis Pertanyaan<span className="text-red-500">*</span>
                        </label>
                        <select
                          value={question.type}
                          onChange={(e) => updateQuestion(question.id, 'type', e.target.value)}
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
                        >
                          {questionTypeOptions.map(option => (
                            <option key={option.value} value={option.value}>
                              {option.label}
                            </option>
                          ))}
                        </select>
                      </div>
                      <div className="mb-4">
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                          Label pertanyaan<span className="text-red-500">*</span>
                        </label>
                        <input
                          type="text"
                          value={question.label}
                          onChange={(e) => updateQuestion(question.id, 'label', e.target.value)}
                          placeholder="New Question"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
                        />
                      </div>

                      {/* Options for radio, checkbox, dropdown */}
                      {['radio', 'checkbox', 'dropdown'].includes(question.type) && (
                        <div className="mb-4">
                          <label className="block text-sm font-medium text-gray-700 mb-2">
                            Options
                          </label>
                          <div className="space-y-2">
                            {question.options?.map((option, optIdx) => (
                              <div key={optIdx} className="flex items-center gap-2">
                                <input
                                  type="text"
                                  value={option}
                                  onChange={(e) => updateOption(question.id, optIdx, e.target.value)}
                                  placeholder={`Option ${optIdx + 1}`}
                                  className="flex-1 px-3 py-2 border border-gray-300 rounded focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
                                />
                                <button
                                  onClick={() => removeOption(question.id, optIdx)}
                                  className="p-2 text-red-500 hover:bg-red-50 rounded transition-colors"
                                >
                                  <Trash2 className="w-4 h-4" />
                                </button>
                              </div>
                            ))}
                            <button
                              onClick={() => addOption(question.id)}
                              className="text-sm text-red-500 hover:text-red-600 font-medium"
                            >
                              + Add Option
                            </button>
                          </div>
                        </div>
                      )}

                      <label className="flex items-center gap-2 cursor-pointer">
                        <input
                          type="checkbox"
                          checked={question.required}
                          onChange={(e) => updateQuestion(question.id, 'required', e.target.checked)}
                          className="w-4 h-4 text-red-500 border-gray-300 rounded focus:ring-red-500"
                        />
                        <span className="text-sm text-gray-700">Harus diisi</span>
                      </label>
                    </div>
                    <button
                      onClick={() => removeQuestion(question.id)}
                      className="p-2 text-red-500 hover:bg-red-50 rounded transition-colors"
                    >
                      <Trash2 className="w-5 h-5" />
                    </button>
                  </div>
                </div>
              ))}
            </div>
          )}

          <button
            onClick={addQuestion}
            className="w-full bg-red-500 hover:bg-red-600 text-white font-medium py-3 rounded-lg transition-colors flex items-center justify-center gap-2"
          >
            <Plus className="w-5 h-5" />
            Tambah Pertanyaan
          </button>
        </div>
      </div>
    </div>
  );
}